@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
body {
    font-family: 'Ubuntu', sans-serif;
  }



.App__wrapper{
    margin: 0 auto;
    /* width: 1800px; */
    background-color: #373AF5;
    color: white;
    position: relative;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: space-between;
            align-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    background-image:  url(/Weather-App/static/media/bottom.071ea89a.png), url(/Weather-App/static/media/up.1b9e0722.png);  
    background-position: left bottom , right top ;
    background-size: 500px 700px;
    background-repeat: no-repeat, no-repeat;
}
.Content__wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-content: space-between;
            align-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}
.App__title{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

    width: 100%;
    font-weight: 700;
    font-size: 100px;
}
.App__title-sub{
    padding-left: 350px;
}
.App__footer{
    font-size: 14px;
    line-height: 18px;
    opacity: 0.6;
    text-transform: uppercase;
    padding-bottom: 16px;
}
.weekForecast, .dayForecast {
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 524px;
    padding: 50px;
    margin: 17px;
    background-color: #fff;
    box-shadow: 0px 4px 4px rgba(4, 5, 73, 0.25), 14px 14px 20px rgba(5, 6, 114, 0.2);
    border-radius: 8px; 
}

.weekForecast__title, .dayForecast__title {
    font-size: 32px;
    color: #2C2D76
}

.dayForecast__card{
    padding-top: 50px;
}
.dayForecast__form{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.Warning{
    padding-top: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: space-between;
            align-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: #8083A4;
    font-weight: bold;
    font-size: 16px;
}
.Warning__title{
    padding-top: 30px;
}

.WeatherCard{
    height: 240px;
    position: relative;
    padding: 20px;
    margin: 5px;
    background-color: #373AF5;
    
    -webkit-flex-basis: 100%;
    
            flex-basis: 100%;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(4, 5, 73, 0.25), 14px 14px 20px rgba(5, 6, 114, 0.2);
    border-radius: 8px;
}
.WeatherCard__date{
    font-weight: bold;
    font-size: 16px;
}
.WeatherCard__temp{
    font-weight: bold;
    font-size: 32px;
    line-height: 32px;
    position: absolute;
    bottom: 15px;
    right: 15px;
}
.WeatherCard__icon{
    text-align: center;
    margin: 0 auto;
    padding: 30px;
}

.Slider{
    padding-top: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
}
.Slider__container{
    display: -webkit-flex;
    display: flex;
}


.citySelect{
    margin: 10px;
    color: #2C2D76;
    padding: 12px 16px;
    border: 2px solid rgba(128, 131, 164, 0.2);
    box-sizing: border-box;
    border-radius: 8px;
}
.datePicker{
    margin: 10px;
    color: #2C2D76;
    padding: 9px 16px;
    border: 2px solid rgba(128, 131, 164, 0.2);
    box-sizing: border-box;
    border-radius: 8px;
}


